<template>
  <div id="random">
    {{title[$route.params.index]}}
    <div @click="back">返回</div>
  </div>
</template>
<script>
export default ({
  data () {
    return {
      title: [
        '随机分享页1',
        '随机分享页2',
        '随机分享页3',
        '随机分享页4'
      ]
    }
  },
  methods: {
    back () {
      this.$router.go(-1)
    }
  },
  created () {
    document.title = this.title[this.$route.params.index]
  }
})
</script>
<style lang="scss" scoped>

</style>



